.header-wrap .tohome .common-header-item {
  padding: 0 15px;
  cursor: pointer;
  font-weight: 400;
  line-height: 17px;
  color: rgba(255, 255, 255, 0.9);
  height: 17px;
  font-size: 14px;
}
.header-wrap .tohome .header-common-index {
  cursor: pointer;
  font-weight: 400;
  line-height: 17px;
  color: rgba(255, 255, 255, 0.9);
  height: 17px;
  font-size: 14px;
}
.header-wrap .tohome .anticon {
  color: #fff;
  font-size: 15px;
}
.header-wrap .tohome .common-active {
  font-weight: 400;
  line-height: 20px;
  color: #ffcb53;
  opacity: 1;
  height: 20px;
  font-size: 14px;
}
.header-wrap .common-right-box {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}
.header-wrap .common-right-box .m15 {
  margin-right: 20px;
}
.header-wrap .common-right-box .anticon svg {
  color: #fff;
  font-size: 15px;
}
.header-wrap .common-right-box .ant-badge-count {
  height: 15px;
  line-height: 15px;
  width: 15px;
  padding: 0 0!important;
  min-width: 15px;
}
.header-wrap .common-right-box .common-row-box {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
}
.header-wrap .common-right-box .common-row-box .common-user-img {
  position: relative;
  margin-right: 10px;
  width: 32px;
  height: 32px;
  margin-bottom: 10px;
}
.header-wrap .common-right-box .common-row-box .common-name {
  padding: 3px 30px 0 0;
  font-weight: 400;
  line-height: 19px;
  color: rgba(255, 255, 255, 0.6);
  opacity: 1;
  height: 17px;
  font-size: 12px;
  margin-left: 5px;
  margin-bottom: 10px;
}
.header-wrap .common-right-box .common-row-bottom .ant-menu {
  background: none;
}
.st-header {
  display: flex;
  align-items: center;
  position: fixed;
  top: 27px;
  margin-left: -50px;
  z-index: 199;
}
.st-header .icon-header-name {
  font-size: 24px;
  font-weight: bold;
  color: #000000;
  margin-bottom: 0;
}
.st-header .icon-goback {
  width: 21px;
  padding: 5px 7px;
}
.st-header .icon-home {
  width: 32px;
  margin-right: 24px;
}
